@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../fonts/.woff2") format("woff2"), url("../fonts/.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #141414;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.container--inner {
  padding: 0 370px;
}
@media (max-width: 1439px) {
  .container--inner {
    padding: 0 130px;
  }
}
@media (max-width: 1023px) {
  .container--inner {
    padding: 0 48px;
  }
}
@media (max-width: 767px) {
  .container--inner {
    padding: 0 16px;
  }
}

.header {
  padding: 28px 0;
  background-color: #141414;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__search {
  width: 571px;
}

.main-nav__list {
  display: flex;
  margin: 0;
  margin-right: 20px;
  margin-left: 60px;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  margin-right: 20px;
}
.main-nav__link {
  color: #ffffff;
}
.main-nav__link.is-active {
  opacity: 0.6;
}
.main-nav__toggle {
  display: none;
}

.footer {
  padding: 40px 0;
  background-color: #141414;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease, color 0.3s ease;
}
.btn:disabled {
  color: #707070;
  pointer-events: none;
}

.link {
  border: none;
  border-radius: 0;
  background-color: rgba(255, 255, 255, 0);
  color: inherit;
  cursor: pointer;
  transition: opacity 0.3s ease, color 0.3s ease;
}
.link:hover, .link:focus {
  color: #FF5C00;
}
.link:disabled {
  color: #707070;
  pointer-events: none;
}
.link--blue {
  color: #2C80FF;
}
.link--blue:hover, .link--blue:focus {
  color: #2C80FF;
  text-decoration: underline;
}
.link--blue:disabled {
  color: #707070;
}

.title {
  width: 100%;
  margin: 0;
  color: inherit;
}
.title--center {
  text-align: center;
}
.title--h1 {
  margin-bottom: 40px;
  font-weight: 700;
  font-size: 36px;
  line-height: 130%;
}
.title--h2 {
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
}
.title--h3 {
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 24px;
  line-height: 130%;
}
.title--h4 {
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 18px;
  line-height: 130%;
}

.price {
  margin-bottom: 24px;
}

.price__container-value {
  display: flex;
  justify-content: space-between;
}

.price__value {
  position: relative;
  width: 97px;
}
.price__value label {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 6px;
  font-size: 16px;
  line-height: 130%;
  color: #707070;
}
.price__value input {
  width: 100%;
  padding-left: 25px;
  padding-right: 7px;
  padding-top: 3px;
  padding-bottom: 3px;
  font-size: 16px;
  line-height: 130%;
  border: 1px solid #ffffff;
  border-radius: 40px;
  background-color: #141414;
  color: #ffffff;
}

.user-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: flex-end;
}

.user-menu__item {
  position: relative;
  margin-right: 20px;
}
.user-menu__item:last-child {
  margin-right: 0;
}

.user-menu__link svg {
  width: 32px;
  height: 32px;
}

.search {
  position: relative;
  display: flex;
}
.search label {
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 11px;
  padding-bottom: 11px;
  background-color: #ffffff;
  color: #141414;
  border: 1px solid #ffffff;
  border-radius: 40px 0px 0px 40px;
  font-weight: 700;
  font-size: 14px;
  line-height: 120%;
}
.search input {
  width: 100%;
  padding-right: 40px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  background-color: #141414;
  color: #ffffff;
  border: none;
  border: 1px solid #ffffff;
  border-radius: 0 40px 40px 0;
  outline: none;
}
.search--border-radius input {
  border-radius: 40px;
}
.search--margin-16 {
  margin-bottom: 16px;
}

.search__button {
  padding: 0;
  position: absolute;
  right: 17px;
  top: 50%;
  transform: translateY(-50%);
  width: 17px;
  height: 17px;
}
.search__button svg {
  width: 17px;
  height: 17px;
}

.breadcrumbs {
  padding: 0;
  margin: 0;
  margin-bottom: 24px;
  list-style: none;
  display: flex;
}

.breadcrumbs__item {
  position: relative;
  margin: 0;
  color: #ffffff;
  font-size: 14px;
  line-height: 130%;
}
.breadcrumbs__item svg {
  width: 8px;
  height: 8px;
  margin-left: 4px;
  margin-right: 4px;
}

.breadcrumbs__item-current {
  margin-right: 0;
}
.breadcrumbs__item-current::after {
  position: absolute;
  content: none;
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, minmax(200px, 276px));
  grid-gap: 24px;
}

.advantages__item {
  padding-top: 28px;
  padding-left: 21px;
  padding-right: 21px;
  padding-bottom: 28px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid #707070;
  border-radius: 15px;
}

.advantages__description {
  text-align: center;
}

.advantages__img {
  width: 90px;
  height: 90px;
}
.advantages__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.catalog__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, 218px);
  grid-column-gap: 23px;
  grid-row-gap: 16px;
}

.catalog__item {
  min-height: 328px;
}

.card-game {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 18px;
  display: flex;
  align-items: flex-end;
  border-radius: 15px;
}
.card-game::before {
  position: absolute;
  content: none;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  padding-top: 2px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-weight: 700;
  font-size: 12px;
  line-height: 100%;
  text-transform: uppercase;
  text-align: center;
  background: #141414;
  border-radius: 0px 0px 14px 14px;
  z-index: 5;
}
.card-game--new-label::before {
  content: "новинка";
  color: #00FF66;
}
.card-game--promotion-label::before {
  content: "акция";
  color: #00FFF0;
}
.card-game--discont-label::before {
  content: "скидка";
  color: #ffffff;
}
.card-game:hover .card-game__img--shadow::after, .card-game:focus .card-game__img--shadow::after {
  z-index: 0;
  opacity: 1;
}
.card-game:hover .card-game__img--shadow::before, .card-game:focus .card-game__img--shadow::before {
  z-index: 1;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.card-game__img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
  border-radius: 15px;
}
.card-game__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 15px;
}
.card-game__img--shadow::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-image: linear-gradient(180deg, rgba(81, 81, 81, 0) 40.55%, rgba(38, 38, 38, 0.52) 56.1%, rgba(36, 36, 36, 0.91) 72.56%, #333333 100%);
  transform: matrix(-1, 0, 0, 1, 0, 0);
  border-radius: 15px;
  opacity: 1;
  transition: opacity 0.3s ease;
}
.card-game__img--shadow::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-image: linear-gradient(180deg, rgba(81, 81, 81, 0.18) 0%, rgba(81, 81, 81, 0) 0%, rgba(38, 38, 38, 0.52) 56.1%, rgba(36, 36, 36, 0.91) 74.48%, rgba(54, 54, 54, 0.946006) 85.14%, #525252 94.97%);
  transform: matrix(-1, 0, 0, 1, 0, 0);
  border-radius: 15px;
  opacity: 0;
}

.card-game__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
}

.card-game__old-price {
  font-weight: 700;
  font-size: 12px;
  color: #707070;
}

.card-game__price {
  font-weight: 700;
  font-size: 18px;
  line-height: 130%;
  color: #FF5C00;
}

.card-game__title {
  margin-bottom: 4px;
}

.accordion__container {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease;
}

.accordion__list {
  max-height: 190px;
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 16px;
  line-height: 130%;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #707070;
}
.accordion__list::-webkit-scrollbar {
  width: 4px;
  background: #707070;
}

.accordion__button {
  position: relative;
  display: flex;
  justify-content: space-between;
  background-color: inherit;
  border: none;
  cursor: pointer;
}
.accordion__button svg {
  transform: rotate(180deg);
}
.accordion__button:hover svg, .accordion__button:focus svg {
  color: #FF5C00;
}
.accordion__button.is-open svg {
  transform: rotate(0);
}

.accordion__button.is-open + .accordion__container {
  max-height: 300px;
  transition: max-height 0.3s ease;
}

.accordion__item {
  margin-bottom: 24px;
}
.accordion__item:last-child {
  margin-bottom: 0;
}

.filter-input + label {
  cursor: pointer;
}
.filter-input + label::before {
  display: inline-block;
  content: "";
  width: 16px;
  height: 16px;
  margin-right: 8px;
  background-image: url(../img/svg/icon-checkbox.svg);
  background-repeat: no-repeat;
  background-position: 0 0;
}

.filter-input:hover + label,
.filter-input:focus + label {
  color: #FF5C00;
}

.filter-input:disabled + label {
  color: opacify 0.5;
}

.filter-input:checked + label::before {
  background-image: url(../img/svg/icon-checkbox-on.svg);
}

.filter__fieldset {
  margin: 0;
  padding: 0;
  margin-bottom: 24px;
  border: none;
  border-bottom: 1px dashed #707070;
}

.filter__title {
  width: 100%;
  padding: 0;
  margin: 0;
  padding-bottom: 24px;
  font-weight: 700;
  font-size: 18px;
  line-height: 100%;
  color: #ffffff;
}

.page-catalog__column {
  display: grid;
  grid-template-columns: 217px 1fr;
  grid-gap: 21px;
}

.page-catalog__count {
  font-weight: 700;
  font-size: 16px;
  line-height: 130%;
  color: #FF5C00;
  vertical-align: top;
}

.pagination {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  font-weight: 700;
  font-size: 32px;
  line-height: 100%;
  color: #707070;
}
.pagination .pagination__item {
  margin-right: 16px;
}
.pagination .pagination__item:last-child {
  margin-right: 0;
}
.pagination .pagination__item .link {
  display: flex;
}
.pagination .pagination__item--arrow-rotate .link svg {
  transform: rotate(180deg);
}
.pagination .pagination__item--font-size-20 {
  font-size: 20px;
}
.pagination .pagination__item.is-current {
  color: #ffffff;
}
.pagination .pagination__item.is-current .link {
  cursor: default;
}
.pagination .pagination__item.is-current .link:hover,
.pagination .pagination__item.is-current .link:focus {
  color: #ffffff;
